import { NavLink } from 'react-router-dom';
import jmicon from '../../../img/CMS JOOMLA(3).svg'

const JOOMLA = () => {
    return(
        <div className="container cours">

            <div className="row d-flex align-items-center justify-content-between crs-mr">
                <div className="col align-items-center">
                    <h3 className="h1">CMS JOOMLA</h3>
                    <h3 className="lead">50 000р.</h3>
                </div>

                <div className="col img-right">
                    <img src={ jmicon } width="250" className="img-fluid" alt={ jmicon } />
                </div>
            </div>
            
            <p className="lead">
                — одна из популярнейших бесплатных и наиболее мощных Систем Управления Контентом (Content Management System - CMS). Это идеальное решение для тех, кто хочет научиться создавать простые сайты за короткий срок. НО ! Данный курс подойдет только для web разработчиков со знаниями html css js.
            </p>

            <h4>В программу курса входит:</h4>

            <ul className="lead">
                <li key={Math.random()}>Знакомство с CMS Joomla</li>
                <li key={Math.random()}>Выбор доменного имени для сайта. Хостинг для сайта, работающего на CMS Joomla</li>
                <li key={Math.random()}>Установка CMS Joomla</li>
                <li key={Math.random()}>Настройка CMS Joomla.</li>
                <li key={Math.random()}>Управление контентом.</li>
                <li key={Math.random()}>Дизайн сайта.</li>
                <li key={Math.random()}>Расширения CMS Joomla (компоненты, модули, плагины)</li>
                <li key={Math.random()}>Развертывание Интернет-магазина в CMS Joomla</li>
                <li key={Math.random()}>Основы поисковой оптимизации и продвижения сайта на основе CMS Joomla</li>
                <li key={Math.random()}>И другое</li>
            </ul>

            <p className="lead">
                Занятия проходят в удобное для ученика и преподавателя время (пару раз в неделю по 2-3 часа). Расписание составляется при заключении договора. Вы обучаетесь в малокомплектной группе (до 5 человек), начать обучение можно любой момент, как только Вы будете готовы приступить к занятиям. По окончанию курса Вас ожидает выпускной экзамен, Сертификат который котируется у работодателей, а так же пожизненная тех поддержка от вашего преподавателя и помощь в трудоустройстве!
            </p>

            <p className="lead">
                Для расчета стоимости выберите вариант который Вам подходит: индивидуальные занятия 1500 руб / 1 ак час или групповые 1000 руб / 1 ак час - и умножьте стоимость на общее количество академических часов данного курса. Оплата возможна в 2 части.
            </p>
            
            <p className="lead">
                Курс рассчитан на 48 академических часов. Длительность 1,5-2,5 месяца (при посещении 3 занятия в неделю по 3 академических часа - график можно обсудить на собеседовании/тестировании в школе).
            </p>

            <p className="lead">
                online: занятия дистанционно<br />
                offline: занятия с посещением школы
            </p>

            <NavLink to="/Cours"><button className="btn btn-danger btn-lg">Назад</button></NavLink>

        </div>
    )
}

export default JOOMLA;